/*-REVISED STYLE-*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.section {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  width: 90vw;
  overflow: hidden;
}

.login {
  position: relative;
  padding: 40px;
  width: 360px;
  height: 480px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 6px;
  box-shadow: 0 5px 35px rgba(0, 0, 0, 0.2);
  margin-bottom: 150px;
  margin-left: 200px;
}

.login::after {
  content: "";
  position: absolute;
  top: 5px;
  left: 5px;
  right: 5px;
  bottom: 5px;
  border-radius: 5px;
  pointer-events: none;
  background: linear-gradient(
    to bottom,
    rgba(255, 255, 255, 0.3) 0%,
    rgba(255, 255, 255, 0.1) 15%,
    transparent 50%,
    transparent 85%,
    rgba(255, 255, 255, 0.3) 100%
  );
}

.login .login-container {
  position: relative;
  width: 100%;
}

.login .login-container h1 {
  margin-bottom: 30px;
  text-shadow: #7b9aa6;
  color: #7b9aa6;
  text-shadow: black 5px 2px;
}

.login .login-container h5 {
  color: #a69b8f;
  letter-spacing: 2px;
  margin-bottom: 5px;
}

.login .login-container .inputBox {
  position: relative;
  width: 100%;
  margin-bottom: 20px;
}

.login .login-container .inputBox input {
  width: 100%;
  outline: none;
  border: 1px solid rgba(255, 255, 255, 0.2);
  background: transparent;
  padding: 8px 10px;
  border-radius: 6px;
  color: #7b9aa6;
  font-size: 16px;
  font-weight: 700;
  box-shadow: inset 0 0 25px rgba(0, 0, 0, 0.2);
}

.remember {
  display: inline-block;
  color: #f2f2f2;
  font-weight: 400;
  margin-bottom: 10px;
  margin-left: 25px;
  cursor: pointer;
  align-items: center;
}

.remember input {
  margin: 5px;
}

.login__signInButton,
.login__registerButton {
  flex-direction: row;
  background: rgb(123, 154, 166, 0.6);
  border-radius: 6px;
  color: #f2f2f2;
  max-width: 100px;
  margin: 10px;
  padding: 8px 10px;
  box-shadow: none;
}
.buttonBox button:hover,
.buttonBox button:focus {
  /*darker color for hover*/
  background: #506269;
  color: black;
  font-weight: 500;
  transition: background-color 0.5s ease-out;
}

.buttonBox {
  display: flex;
  flex-direction: row;
  margin: 10px;
}

.logo .icon {
  font-size: 45rem;
  margin-left: -525px;
  padding: 20px;
}
@media (max-width: 700px) {
  .login {
    margin-left: 10px;
  }
  .login .login-container {
    position: center;
    width: 100%;
  }
  .logo .icon {
    font-size: 25rem;
    margin-left: -380px;
    margin-top: -200px;
    padding: 20px;
  }
}

* {
  box-sizing: border-box;
}

.contact {
  box-sizing: border-box;
  background: #f2f2f2;
  color: #a69b8f;
  line-height: 1.5;
  padding: 2em;
  /*PICK PROJECT FONT*/
  max-width: 1170px;
  margin-left: auto;
  margin-right: auto;
}

ul {
  list-style-type: none;
  padding: 0;
}

/*-information style-*/
.logo {
  text-align: center;
}

.logo span {
  color: red;
}

.wrap {
  box-shadow: 0 0 20px 0 #a69b8f;
  background: #7b9aa6;
}
/*-everything in wrapper get padding-*/
.wrap > * {
  padding: 1em;
}

.contact-info {
  background: #7b9aa6;
  color: black;
}

.contact-info h3,
.contact-info ul {
  text-align: center;
  margin: 0 0 1rem 0;
}

.contact-note {
  background: #f2f2f2;
}
/*-contact style-*/
.contactIcon {
  margin-right: 5px;
}

.contact form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
}

.contact form label {
  display: block;
  color: black;
  font-size: 14px;
}

.contact form p {
  margin: 0;
}
.contact form .bottom {
  grid-column: 1 / 3;
}

.contact form button,
.contact form input,
.contact form textarea {
  width: 100%;
  padding: 1em;
}

.contact form input {
  height: 32px;
  color: black;
}

.contact form button {
  background: #7b9aa6;
  border-radius: 2px;
  border: 1px solid;
  border-color: #a88734 #9c7e31 #846a29;
  text-transform: uppercase;
}

.contact form button:hover,
.contact form button:focus {
  /*darker color for hover*/
  background: #506269;
  color: black;
  font-weight: 700;
  transition: background-color 0.5s ease-out;
}

.animate__animated .animate__heartBeat {
  /* --animate-duration: 10s; */
  --animate-duration: 8000ms;
}

/*LARGE SCALE*/
@media (min-width: 700px) {
  /* body {
    display: none;
  } */
  .wrap {
    display: grid;
    grid-template-columns: 1fr 2fr;
  }
  .wrap > * {
    padding: 2em;
  }
  .contact-info h3,
  .contact-info ul,
  .logo {
    text-align: left;
  }
}

