.landing2 {
  width: 90vw;
  margin-left: 20px;
  margin-right: 20px;

  height: 100%;
  flex-grow: 1;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-column-gap: 10px;
  grid-row-gap: 10px;
  grid-template-areas:
    " icon1 pic1 pic2 "
    " info1 info1 pic2 "
    " pic3 icon2 pic2 "
    " pic3 info2 info2 "
    " pic3 pic4 icon3 "
    " info3 info3 info3 ";
}

.icon1 {
  grid-area: icon1;
  background: #a69b8f;
  object-fit: fill;
}
.icon2 {
  grid-area: icon2;
  background: #a69b8f;
}
.icon3 {
  grid-area: icon3;
  background: yellow;
  background: url(/static/media/360close.3868cd9a.jpg);
  max-width: 350px;
  max-height: 275px;
}
.info1 {
  grid-area: info1;
  background: #7b9aa6;
}
.info2 {
  grid-area: info2;
  background: #7b9aa6;
  padding: 30px;
  color: #f2f2f2;
  display: grid;
  grid-column-gap: 5px;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: " dir1 dir2 ";
}
.info2 .dir1 {
  grid-area: dir1;
}
.info2 .dir2 {
  grid-area: dir2;
}
.info2 li {
  padding: 10px;
  font-size: 18px;
  font-weight: 700;
  list-style-type: square;
}
.info3 {
  grid-area: info3;
  background: #7b9aa6;
}

.landing2 .icon {
  font-size: 16rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgb(0, 0, 0, 0.2);
  margin-left: 65px;
