/*sECONDARY sTYLE*/
body {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #f2f2f2;
}

.store {
  margin-top: 50px;
  position: relative;
  width: 1100px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 30px;
}

.store .cards {
  max-width: 300px;
  position: relative;
  height: 100px;
  background: #a69b8f;
  margin: 30px 10px;
  padding: 20px 15px;
  display: flex;
  flex-direction: column;
  box-shadow: 0 5px 202px rgba(0, 0, 0, 0.5);
  transition: 0.3s ease-in-out;
}

.store .cards:hover {
  height: 420px;
}

.store .cards .card-image {
  position: relative;
  width: 250px;
  height: 250px;
  left: 10px;
  top: -65px;
  box-shadow: 0 5px 202px rgba(0, 0, 0, 0.3);
  z-index: 1;
}

.store .cards .card-image {
  max-width: 100%;
  border-radius: 4px;
}

.store .cards .card-info {
  position: relative;
  margin-top: -240px;
  padding: 10px 15px;
  text-align: center;
  color: #111;
  visibility: hidden;
  opacity: 0;
  transition: 0.3s ease-in-out;
}

.store .cards:hover .card-info {
  visibility: visible;
  opacity: 1;
  margin-top: -40px;
  transition-delay: 0.3s;
}

